
import { Row, Col, Card, CardBody, CardImg, Media, Container, Button } from "reactstrap";
import { FaFacebookF, FaTwitter, FaInstagram } from "react-icons/fa";
import { Link } from "react-router-dom";
import Frame2 from "../../assets/img/Trenches_Pas/frame2.png"
import Frame3 from "../../assets/img/Trenches_Pas/frame3.png"
import Frame4 from "../../assets/img/Trenches_Pas/frame4.png"
import { useEffect, useState } from "react";
export default function Sub_Section() {

    const [timerDays, setTimerDays] = useState(0);
    const [timerHours, setTimerHours] = useState(0);
    const [timerMinutes, setTimerMinutes] = useState(0);
    const [timerSeconds, setTimerSeconds] = useState(0);
    let interval;

    const startTimer = () => {
        const countdownDate = new Date('November 17, 2022 00:00:00').getTime();

        interval = setInterval(() => {
            const now = Date.now();
            const difference = countdownDate - now;

            const days = Math.floor(difference / (1000 * 60 * 60 * 24));
            const hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
            const minutes = Math.floor((difference / 1000 / 60) % 60);
            const seconds = Math.floor((difference / 1000) % 60)

            if (difference < 0) {
                clearInterval(interval.current)
            }
            else {
                setTimerDays(days);
                setTimerHours(hours);
                setTimerMinutes(minutes);
                setTimerSeconds(seconds);
            }
        }, 1000)
    };

    useEffect(() => {
        startTimer();
        return () => {
            clearInterval(interval.current)
        }
    })

    return (
        <>
            <section className="second-sec second-sec-image" id="benefits">
                <div className="">
                    <div data-aos="fade-down">
                        <div className="container pb-3">
                            <Row className="d-flex justify-content-center align-items-center">
                                <Col lg='6' md='12' sm='12' xs='12'>
                                    <p className="text-uppercase text-center text-lg-left text1 text-white">Trenches Pass NFT holders will get special benefits</p>
                                </Col>
                                <Col lg='6' md='12' sm='12' xs='12' className="d-flex justify-content-center justify-content-lg-end">
                                    <img src={require('../../assets/img/Trenches_Pas/ellipse.png').default} width={30} height={30} className="d-none d-lg-block" />
                                    <a href="https://www.premint.xyz/exceed/" target="_blank" className="ml-5" >
                                        <Button type="button" className="sec-2-button text-right mt-4 mt-lg-0">Join Allow List</Button>
                                    </a>
                                </Col>
                            </Row>
                        </div>
                    </div>
                </div>
                <div className="px-0 mt-5" data-aos="zoom-in">
                    <Container>
                        <Row className="">
                            <Col xl="3" lg="6" xs="12" sm="12" md="6" className="mb-4 mb-xl-0">
                                <div className="border-benefit py-5 px-3">
                                    <img src={Frame2} width={80} className="img-fluid benefit-img" />
                                    <h4 className="text-white text-uppercase mt-5">priority listing for Reg A offering</h4>
                                    <p className="text-white mt-4">Exclusive priority list reservation for first dibs on Talentshares in the upcoming Lil Durk IPO for his next single 'Bedtime'.</p>
                                </div>
                            </Col>
                            <Col xl="3" lg="6" xs="12" sm="12" md="6" className="mb-4 mb-xl-0">
                                <div className="border-benefit py-5 px-3">
                                    <img src={Frame3} width={80} className="img-fluid benefit-img" />
                                    <h4 className="text-white text-uppercase mt-5">Exclusive access to Trenches Pass GTA server</h4>
                                    <p className="text-white mt-3">Skip to the front of the 15k person waitlist and receive immediate and lifetime access to the Trenches GTA Server.</p>
                                </div>
                            </Col>
                            <Col xl="3" lg="6" xs="12" sm="12" md="6" className="mb-4 mb-xl-0">
                                <div className="border-benefit py-5 px-3">
                                    <img src={Frame4} width={80} className="img-fluid py-2 pl-1 benefit-img" />
                                    <h4 className="text-white text-uppercase mt-5">Trenches GTA Online Loot Box</h4>
                                    <p className="text-white mt-4">Containing 7 unique combinations of custom created in-game wearables made exclusively available to Trenches Pass holders (2,400+ traits across the 7 different attributes).</p>
                                </div>
                            </Col>
                            <Col xl="3" lg="6" xs="12" sm="12" md="6">
                                <div className="border-benefit py-5 px-3">
                                    <img src={Frame2} width={80} className="img-fluid benefit-img" />
                                    <h4 className="text-white text-uppercase mt-5">Up to 5 IRL perks (1 per NFT)</h4>
                                    <p className="text-white mt-4">Access to Private discord channel, VIP to all future Trenches online events and concerts, Limited edition OTF/Trenches/Lil Durk Merch, Durk Wardrobe from tour, VIP concert tickets, Social media handle listed in the description box and credits of the ‘Bedtime’ music video.</p>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </section>
            <section className="second-sec">
                <div className="container pb-3">
                    <Row className="d-flex justify-content-center align-items-lg-center mt-3 mb-5">
                        <Col lg='12' md='12' sm='12' xs='12' className="d-flex justify-content-center justify-content-lg-end">
                            <a href="https://www.premint.xyz/exceed/" target="_blank">
                                <Button type="button" className="sec-2-button mb-5">Join Allow List</Button>
                            </a>
                        </Col>
                        <Col lg='4' md='12' sm='12' xs='12' className="d-flex justify-content-center">
                            <div className="sec2-card p-3 py-5">
                                <h1 className="text-white">3,333</h1>
                                <p className="text-white">
                                    Exclusive Lil Durk NFT passes available to mint on the Ethereum Blockchain.
                                </p>
                            </div>
                        </Col>
                        <Col lg='8' md='12' sm='12' xs='12' className="countdown d-lg-flex justify-content-lg-end mt-5 mt-lg-0" id="launch_date">
                            <div className="timer-sec">
                                <h1 className="text-white text-center text1 mt-5">LAUNCH DATE</h1>
                                <h3 className="text-center text-white">November 17</h3>
                                <div className="d-flex flex-row justify-content-center">
                                    <div className="timer d-flex flex-column justify-content-center align-items-center mr-3"><div className="timer-number font-weight-bold pz-title">{timerDays < 10 ? "0" + timerDays : timerDays}</div> <div className="mt-2 text-light-black">DAYS</div></div>
                                    <div className="timer d-flex flex-column justify-content-center align-items-center mr-3"><div className="timer-number font-weight-bold pz-title">{timerHours < 10 ? "0" + timerHours : timerHours}</div> <div className="mt-2 text-light-black">HOURS</div></div>
                                    <div className="timer  d-flex flex-column justify-content-center align-items-center mr-3"><div className="timer-number font-weight-bold pz-title">{timerMinutes < 10 ? "0" + timerMinutes : timerMinutes}</div> <div className="mt-2 text-light-black">MIN</div></div>
                                    <div className="timer  d-flex flex-column justify-content-center align-items-center mr-3"><div className="timer-number font-weight-bold pz-title">{timerSeconds < 10 ? "0" + timerSeconds : timerSeconds}</div> <div className="mt-2 text-light-black">SEC</div></div>
                                </div>
                            </div>
                        </Col>
                    </Row>

                </div>
            </section>
        </>
    )
}