import React from "react";

import { Row, Col, Button, Container } from "reactstrap";
import Img1 from "../assets/img/Trenches_Pas/img1.png"
const NovemberSec = () => {
    return (
        <section className="bg-section4 pb-0">
            <Container>
                <Row className="d-flex justify-content-center align-items-center">
                    <Col lg="12" md='12' sm="12" xs="12">
                        <div className="sec-4img text-center">
                            <img src={Img1} alt="exceed" width={600} />
                            <a href="https://www.premint.xyz/exceed/" target="_blank">
                                <Button type="button" className="align-bottom mb-3 mt-3 sec-2-button">Join Allow List</Button>
                            </a>
                        </div>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}
export default NovemberSec