import { Row, Col, Container } from "reactstrap";
import IndexNavbar from "components/Navbars/IndexNavbar";
import Img1 from "../../assets/img/Trenches_Pas/img1.png"
import Vector1 from "../../assets/img/Trenches_Pas/vector1.png"

export default function Banner() {

  return (
    <div className="banner">
      <div>
        <IndexNavbar className="fixed-top" />
      </div>
      <div className="banner-section container-fluid position-rel mt-5">
        <Row className="text-center">
          <Col lg="6" xs="12">
            <h1 className="text-3d ">TRENCHES</h1>
          </Col>
          <Col lg="6" xs="12" className="">
            <h1 className="text-3d text-lg-right">PASS</h1>
          </Col>
          <Col lg="12" xs="12" className="position-abs">
            <img src={Img1} alt="Exceed" className="sec1-img" />
          </Col>
          <Col lg="12" className="position-abs1">
            <img src={Vector1} alt="Exceed" className="vector1-css text-center" />
          </Col>
        </Row>
      </div>
    </div>

  )
}