import React from "react";
import { Link } from "react-router-dom";
import { AnchorScroll } from "helper";
import Facebook from "../../assets/img/Trenches_Pas/facebook.png"
import Twitter from "../../assets/img/Trenches_Pas/twitter.png"
import Youtube from "../../assets/img/Trenches_Pas/youtube.png"
// reactstrap components
import {

  Collapse,
  NavbarBrand,
  Navbar,
  Container,
  NavItem,
  NavLink,
  Nav,
  Row,
  Col,
  Button
} from "reactstrap";
import { FaDiscord, FaTwitter, FaTimes } from "react-icons/fa";
import { FiMenu } from 'react-icons/fi';


export default function IndexNavbar() {
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [collapseOut, setCollapseOut] = React.useState("");

  const toggleCollapse = () => {
    document.documentElement.classList.toggle("nav-open");
    setCollapseOpen(!collapseOpen);
  };
  const onCollapseExiting = () => {
    setCollapseOut("collapsing-out");
  };
  const onCollapseExited = () => {
    setCollapseOut("");
  };

  return (
    <Navbar expand="xl" className="bg-nav">
      <Container>
        <Row className="w-100">
          <Col xl="4">
            <div>
              <NavbarBrand to="/" tag={Link} id="navbar-brand" onClick={() => AnchorScroll()}>
                <img src={require('../../assets/img/logo.png').default} alt='Exceedlogo' className='img-fluid' /><span className="logo-black">EXCEED</span>
              </NavbarBrand>
              <button
                aria-expanded={collapseOpen}
                className="navbar-toggler navbar-toggler"
                onClick={toggleCollapse}
              >
                <FiMenu className="text-white" size="24" />
              </button>
            </div>
          </Col>
          <Col xl="8" className="d-flex justify-content-center pr-0">
            <Collapse
              className={"ml-4 justify-content-between " + collapseOut}
              navbar
              isOpen={collapseOpen}
              onExiting={onCollapseExiting}
              onExited={onCollapseExited}
            >
              <div className="d-flex justify-content-between">
                <div className="d-flex justify-content-between">
                  <Nav navbar className="nav-text">
                    <NavItem className="px-2">
                      <NavLink
                        data-placement="bottom"
                        rel="noopener noreferrer"
                      >
                        <a href="#launch_date">LAUNCH DATE</a>
                      </NavLink>
                    </NavItem>
                    <NavItem className="px-2">
                      <NavLink
                        data-placement="bottom"
                        rel="noopener noreferrer"
                      >
                        <a href="#roadmap">ROADMAP</a>
                      </NavLink>
                    </NavItem>
                    <NavItem className="px-2">
                      <NavLink
                        data-placement="bottom"
                        rel="noopener noreferrer"
                      >
                        <a href="#benefits">REWARDS</a>
                      </NavLink>
                    </NavItem>
                  </Nav>
                  <div className="social-div mt-4 mt-xl-0 ml-xl-4">
                    <a href="https://twitter.com/ExceedFunds" target={'_blank'} rel='noreferrer'><FaTwitter size="35" color="black" className="icon-rounded" /></a>
                    <a href="http://discord.gg/BbYnNJBATb" target={'_blank'} rel='noreferrer'><FaDiscord size="35" color="black" className="icon-rounded" /></a>
                  </div>
                </div>
                <div className="navbar-collapse-header">
                  <Row>
                    <Col className="collapse-close" xs="6">
                      <button
                        aria-expanded={collapseOpen}
                        onClick={toggleCollapse}
                      >
                        <FaTimes className="pf-text-secondary" size="24" />
                      </button>
                    </Col>
                  </Row>
                </div>
              </div>
            </Collapse>
          </Col>
        </Row>
      </Container>
    </Navbar>
  );
}
